@import "../colors";

.imagesection {
    display: flex;
    margin-top: 4rem;
    margin-bottom: 4rem;

    .img {        
        width: 200%;
        height: 30vh;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .about-info {
        margin-left: 2rem;

        h4 {
            font-size: 2rem;
        }

        .about-text {
            padding: 1rem 0;
        }
    }
}

.about-details {
    display: flex;

    p {
        padding: .4rem 0;
    }

    .left-section {

    }

    .right-section {
        margin-left: 2.3rem;
    }
}

.btn {
    padding: 1rem 2rem;
    background-color: $main-blue-color;
    outline: none;
    border: none;
    font-family: inherit;
    font-size: inherit;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
    letter-spacing: 2px;
    margin-top: 1rem;
    position: relative;

    &::after {
        position: absolute;
        content: "";
        width: 0;
        height: 0.3rem;
        left: 0;
        bottom: 0;
        background: $main-blue-color-faded;

        transition: all .2s ease;
    }

    &:hover::after {
        width: 100%;
    }
}

.skill-container {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: 4rem;
    row-gap: 3rem;
    margin: 4rem 0;
}