@import "../colors";

.App {
    .sidebar {
        width: 16%;
        height: 100%; 
        background-color: $sidebar; 
        position: fixed;
        border-right: 1px solid $main-border-color;
    }

    .main-content {
        width: 84%;
        margin-left: 16%;
        background-color: $bg;
        min-height: 100vh;
        background-image: url(../img/dots.svg);
        display: grid;

        .content {
            margin: 5rem 15rem;
        }
    }
}