@import "../colors";

.homepage {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-40%, -50%);
    width: 70%;

}

.hero-text {
    color: white;
    text-align: center;
    font-size: 3.6rem;
    span {
        color: $main-blue-color;
    }
}

.h-sub-text {
    text-align: center;

    span {
        color: $main-blue-color;
        font-size: 1.5rem;
        font-weight: 1000;
    }

    a {
        text-decoration: none;
        color: $main-blue-color;
        font-weight: 500;
    }
}

.icons {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 0;

    .icon-holder {
        color: $font-color;
        text-decoration: none;
        padding: 0 1rem;
        font-size: 1.8rem;
        background-color: transparent;

        span {
            font-size: 0;
            background-color: transparent;
            transition: all 0.5s ease;
            color: $main-blue-color-faded;

            a {
                text-decoration: none;
                color: inherit;
            }

            &.active {
                font-size: 1.8rem;
            }
        }

        .icon {
            font-size: 1.8rem;
            color: white;

            &:hover {
                color: $main-blue-color;
            }
        }
    }
}