@import "./Styles/layout";
@import "./Styles/navbar";
@import "./Styles/typography";
@import "./Styles/title";
@import "./Styles/about";
@import "./Styles/skills";

@import "./colors";

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scrollbar-width: none; // firefox
    &::-webkit-scrollbar {
        display: none;
    } // Chrome, Safari
}

body {
    color: $font-color;
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 1.2rem;
}

span {
    color: $main-blue-color;
}