@import "../colors.scss";

.skillssection {

    // margin-top: 1.4rem;

    .skills-container {

        .skill-title {
            font-size: 1.4rem;
            font-weight: 600;
        }

    }

}

.skill-bar {
    display: flex;
    align-items: center;

    .skill-text {

    }

    .skill {

        width: 1000%;

        .progress {
            width: 100%;
            height: 0.7rem;
            background: $main-blue-color-reduced;
            margin-left: 1rem;
            position: relative;
            border-radius: 10px;

            .inner-progress {
                position: absolute;
                // width: 70%;
                background-color: $main-blue-color;
                height: 100%;
                bottom: 0;
                left: 0;
                border-radius: 10px;
            }
        }
    }

}
